import { formatFileSize, isDefinedGlobally } from './utils'

const messages = {
  _default: field => ` ${field} `,
  after: (field, [target]) => `${field}  ${target}`,
  alpha: field => `${field} `,
  alpha_dash: field =>
    `${field}    (-)  (_)`,
  alpha_num: field => `${field}  `,
  alpha_spaces: field => `${field}  `,
  before: (field, [target]) => `${field}  ${target}`,
  between: (field, [min, max]) =>
    `${field}  ${min}  ${max}`,
  confirmed: field => ` ${field} `,
  credit_card: field => ` ${field} `,
  date_between: (field, [min, max]) =>
    ` ${field}  ${min}  ${max}`,
  date_format: (field, [format]) => ` ${field}  ${format}`,
  decimal: (field, [decimals = '*'] = []) =>
    `${field}   ${
      decimals === '*' ? '' : decimals + ' '
    }`,
  digits: (field, [length]) =>
    `${field}  ${length} `,
  dimensions: (field, [width, height]) =>
    `${field}  ${width}x${height} px`,
  email: field => `${field} `,
  excluded: field => `${field} `,
  ext: field => `${field} `,
  image: field => `${field} `,
  included: field => `${field} `,
  integer: field => `${field} `,
  ip: field => `${field}  IP address`,
  length: (field, [length, max]) => {
    if (max) {
      return `${field}  ${length}  ${max}`
    }

    return `${field}  ${length}`
  },
  max: (field, [length]) => `${field}  ${length} `,
  max_value: (field, [max]) => `${field}  ${max}`,
  mimes: field => `${field} `,
  min: (field, [length]) =>
    `${field}  ${length} `,
  min_value: (field, [min]) => `${field}  ${min} `,
  numeric: field => `${field} `,
  regex: field => ` ${field} `,
  required: field => ` ${field}`,
  size: (field, [size]) =>
    `${field}  ${formatFileSize(size)}`,
  url: field => `${field}  URL `,

  //
  checkId: (field, [len]) =>
    field + ' ' + (len ? len + ' ' : '') + ' ',
  check_age_more_than: (field, value_min) =>
    `${field}  ${value_min} `,
  unique: field => `${field} `
}

const locale = {
  name: 'th',
  messages,
  attributes: {}
}

if (isDefinedGlobally()) {
  // eslint-disable-next-line
  VeeValidate.Validator.localize({ [locale.name]: locale });
}

export default locale
